<template>
  <section class="timeline">
    <h4 class="page_title">{{$t('experience.page_title')}}</h4>
    <div class="divider"></div>
    <div class="timeline-container d-flex layout-column">
      <div class="timeline-item d-flex" v-for="(item, idx) in timeline" :key="item.key">
        <div class="d-flex layout-column ta-right"
             :class="idx % 2 === 0 ? 'time-container' : 'text-container'"
        >
          <template v-if="idx % 2 === 0">
            <div class="time" v-if="item.endTime">{{item.endTime}}</div>
            <div class="time">{{item.startTime}}</div>
          </template>
          <template v-else>
            <div class="title">{{item.title}}</div>
            <div class="subtitle">{{item.subtitle}}</div>
          </template>
        </div>
        <div class="center d-flex jc-center layout-column">
          <div class="marker" :class="item.type"></div>
        </div>
        <div class="d-flex layout-column ta-left"
             :class="idx % 2 !== 0 ? 'time-container' : 'text-container'"
        >
          <template v-if="idx % 2 !== 0">
            <div class="time" v-if="item.endTime">{{item.endTime}}</div>
            <div class="time">{{item.startTime}}</div>
          </template>
          <template v-else>
            <div class="title">{{item.title}}</div>
            <div class="subtitle">{{item.subtitle}}</div>
          </template>
        </div>
      </div>
    </div>

  </section>
</template>

<script>
import locale from '../config/locales/en.json';


export default {
  name: 'Experience',
  computed: {
    timeline() {
      const $t = this.$t.bind(this);
      const { experience: { items = {} } = {} } = locale;
      return Object.keys(items)
        .sort((a, b) => items[b].startTime.localeCompare(items[a].startTime))
        .map(
          key => ({
            type: items[key].endTime ? 'range' : 'event',
            title: $t(`experience.items.${key}.title`),
            subtitle: $t(`experience.items.${key}.subtitle`),
            startTime: $t(`experience.items.${key}.startTime`),
            endTime: items[key].endTime ? $t(`experience.items.${key}.endTime`) : undefined,
          }),
        );
    },
  },
};
</script>

