        content: ' ';
      }

      div.timeline-item {
        margin: 1rem 0;
        z-index: 1;

        div.center {
          width: 2rem;

          .marker {
            border-radius: 0.25rem;
            background: $primary-color-dark;
            height: 100%;

            &.event {
              height: 2rem;
              border-radius: 1rem;
            }
          }
        }

        div.time-container, div.text-container {
          flex-grow: 1;
          max-width: calc(50% - 1rem);
          padding: 0.5rem;
          justify-content: center;

          &.ta-left {
            text-align: left;
