<template>
  <div class="quick-links d-flex jc-space-between">
    <div v-for="link in links" class="link" :class="link.key" :key="link.key">
      <a :href="link.url" :target="link.target"><img :src="link.icon" :alt="key"></a>
    </div>
  </div>
</template>

<script>
import quickLinks from '../config/quick-links';

export default {
  name: 'QuickLinks',
  components: {},
  computed: {
    links() {
      return Object.keys(quickLinks)
        .map(key => ({
          key,
          ...quickLinks[key],
        }));
    },
  },
};
</script>

<style lang="scss" scoped>

  .link {
    img, .icon {
      max-width: 100%;
      display: block;

      svg {
        width: 100% !important;
      }
    }

    opacity: 0.8;

    &:hover {
      opacity: 1;
    }

    margin: 0.5rem 0.25rem;
    cursor: pointer;
  }

</style>
