<template>
  <section :class="{'is-mini':!fullMode}" class="home d-flex layout-column jc-center ai-center">
    <Flipper :flipKey="flipKey" spring="veryGentle">
      <div v-if="fullMode" class="full-mode d-flex layout-column jc-center ai-center">
        <div class="pp-container">
          <Flipped flipId="pp">
            <profile-picture class="pp"></profile-picture>
          </Flipped>
        </div>
        <Flipped flipId="info">
          <div class="info">
            <p class="subtitle light">{{$t('info.title')}}</p>
            <p class="title">{{$t('info.name')}}</p>
            <p class="subtitle dark">{{$t('info.specializations')}}</p>
            <p class="subtitle light">{{$t('info.designation')}} </p>
            <p class="subtitle dark">{{$t('info.current_workplace')}}</p>
            <p class="subtitle light">{{$t('info.current_workplace_address')}}</p>
            <div class="divider"></div>
          </div>
        </Flipped>
        <div class="quick-links-container">
          <Flipped flipId="ql">
            <quick-links></quick-links>
          </Flipped>
        </div>
        <div class="more">
          <div class="divider"></div>
          <div>{{$t('info.learnMore')}}</div>
          <chevron-down></chevron-down>
        </div>
      </div>
      <div class="mini-mode d-flex ai-center z-depth-2" v-else>
        <div flipId="pp" class="pp-container">
          <Flipped flipId="pp">
            <profile-picture class="pp"></profile-picture>
          </Flipped>
        </div>
        <Flipped stagger="wobbly" flipId="info">
          <div class="info flex d-flex layout-column">
            <p class="subtitle light">{{$t('info.title')}}</p>
            <p class="title">{{$t('info.name')}}</p>
            <p class="subtitle light">{{$t('info.specializations')}}</p>
            <div class="quick-links-container">
              <Flipped flipId="ql">
                <quick-links></quick-links>
              </Flipped>
            </div>
          </div>
        </Flipped>
      </div>
    </Flipper>
  </section>
</template>

<script>
import ChevronDown from 'vue-material-design-icons/ChevronDown.vue';
import { Flipper, Flipped } from 'vue-flip-toolkit';
import ProfilePicture from './ProfilePicture.vue';
import QuickLinks from './QuickLinks.vue';

export default {
  name: 'Info',
  components: {
    ProfilePicture,
    QuickLinks,
    Flipped,
    Flipper,
    ChevronDown,
  },
  mounted() {
    this.setupIO();
  },
  data() {
    return {
      fullMode: true,
    };
  },
  methods: {
    setupIO() {
      const self = this;
      const observer = new IntersectionObserver((entries) => {
        // Use `intersectionRatio` because of Edge 15's
        // lack of support for `isIntersecting`.
        // See: https://github.com/w3c/IntersectionObserver/issues/211
        if (entries[0].intersectionRatio <= 0) return;

        // Cleanup the observer when it's not
        // needed anymore.
        // observer.unobserve(this.$el);
        // The `componentFactory()` resolves
        // to the result of a dynamic `import()`
        // which is passed to the `resolveComponent()`
        // function.
        self.fullMode = entries[0].intersectionRatio > 0.75;
      }, {
        root: null,
        threshold: [0, 0.25, 0.5, 0.75, 1],
      });
      // We observe the root `$el` of the
      // mounted loading component to detect
      // when it becomes visible.
      observer.observe(this.$el);
    },
  },
  computed: {
    flipKey() {
      return this.fullMode.toString();
    },
  },
};
</script>

